.panel-switcher {
    position: absolute;
    right: 10px;
    padding:9px 0;
    z-index: 6;

    button {
        color: var(--c-secondary);
        padding:4px;

        div {
            position: absolute;
            right: 0;
            width: 100%;
            height: 100%;
        }
    }
}

.panel {
    position: absolute;
    width: 100%;
    background-color: var(--c-primary);
    transition: opacity 200ms ease-out;
}

@media screen and (max-width: 767px) {
    .panel-switcher {
        .icon-panel{
            width: 1.5rem;
        }
    }

    .panelContainer {
        position: absolute;
        min-height: calc(100% - 50px);
        width: 100vw;
        top: 50px;
        right: 0;
        z-index: 4;
        background:var(--c-primary);
        opacity: 98%;
        transition: opacity 200ms ease-out;
    }

    .panelContainer.panelContainer-hidden {
        opacity: 0;
        min-height: 0;
        height: 0;
        visibility: hidden;
        overflow: hidden;
        transition:
            min-height 0ms ease-out 200ms,
            opacity 200ms ease-out;
    }

    .panel.panel-hidden {
        height: 0;
        opacity: 0;
        visibility: hidden;
        overflow: hidden;
    }
}

@media screen and (min-width: 768px) {
    .panel-switcher {
        position: relative;
        right: unset;
        top: unset;
        pointer-events: none;
    }

    .panelContainer {
        position: relative;
        height: 100%;
        opacity: 1;

        .panel_title { display: none; }
    }

    .items-panel {
        position: relative;
        z-index: 5;

        .items-panel_button { width: 100%; }
    }
    .panel-section { height: 100%; }
}